import React, { useRef } from 'react';
import { useEffectOnce } from 'react-use';
import Scene from 'components/Scene/';
import { ReactComponent as Duck } from 'media/image/duck.svg';
import audio from 'media/audio/crying/';
import './discord.css';

function Discord() {
  const volumeController = useRef(null);
  const observer = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        audio.play();
      } else {
        audio.pause();
      }
    });
  }, { threshold: 0.25, rootMargin: '0px -50% 0px 0px' });

  useEffectOnce(() => {
    observer.observe(volumeController.current);
  }, [observer]);

  return (
    <Scene border name="discord">
      <div className="volumeController" ref={volumeController}>
        <div className="svgContainer duck">
          <Duck />
        </div>
      </div>
    </Scene>
  )
}

export default Discord;
