import React, { useRef, useContext } from 'react';
import SceneVisible from 'context/SceneVisible/';
import playAudioInstance from 'api/playAudioInstance';
import audio from 'media/audio/click/';
import './button.css';

function Button({
  children,
  blurOnInteract = false,
  className,
  callback = () => { return null; }
}) {
  const sceneVisible = useContext(SceneVisible);
  const el = useRef(null);

  const blur = () => {
    el.current.blur();
  };

  return (
    <button
      ref={el}
      className={className}
      tabIndex={(sceneVisible) ? undefined : '-1'}
      onMouseDown={() => playAudioInstance(audio)}
      onTouchStart={() => playAudioInstance(audio)}
      onClick={(event) => {
        if (blurOnInteract) blur();
        callback();
      }}>
      <span className="textMedium">
        {children}
      </span>
    </button>
  );
}

export default Button;
