import React, { useState, useRef, useCallback } from 'react';
import { useKeyPressEvent } from 'react-use';
import './App.css';
import Entry from 'components/Scene/Entry/'
import Surveillance from 'components/Scene/Surveillance/';
import Marginalia from 'components/Scene/Marginalia';
import Discord from 'components/Scene/Discord';
import Grima from 'components/Scene/Grima';
import setMobileVhUnit from 'api/setMobileVhUnit';

function App({ children }) {
  setMobileVhUnit();

  const el = useRef(null);
  const [initiated, setInitiated] = useState(false);
  const [scroll, setScroll] = useState(0);
  const [scrollEnabled, setScrollEnabled] = useState(false);
  const [scrollStartTime, setScrollStartTime] = useState();

  const scrollRight = useCallback((length = 1) => {
    if (scrollEnabled || scroll === 0) {
      setScrollEnabled(false);
      setScroll(Math.max(scroll - length, -1 * el.current.childNodes.length + 1));
      setTimeout(() => {
        setScrollEnabled(true);
      }, 250);
    }
  }, [scroll, scrollEnabled]);

  const scrollLeft = useCallback((length = 1) => {
    if (scrollEnabled) {
      setScrollEnabled(false);
      setScroll(Math.min(scroll + length, -1));
      setTimeout(() => {
        setScrollEnabled(true);
      }, 250);
    }
  }, [scroll, scrollEnabled]);

  const handleWheel = (event) => {
    const currentTime = new Date().getTime();

    if (scrollEnabled && scrollStartTime !== 'undefined') {
      const timeDiff = currentTime - scrollStartTime;
      const threshold = 70;

      if (timeDiff > 250) {
        if ((event.deltaY > threshold || event.deltaX > threshold)
            && scroll > -1 * el.current.childNodes.length) {
          scrollRight();
        } else if ((event.deltaY < threshold * -1 || event.deltaX < threshold * -1) && scroll < -1) {
          scrollLeft();
        }
      }
    }

    setScrollStartTime(currentTime);
  };

  const handleArrowRight = () => {
    if (scrollEnabled && document.activeElement === document.body) {
      if (scroll > -1 * el.current.childNodes.length) {
        scrollRight();
      }
    }
  };

  const handleArrowLeft = () => {
    if (scrollEnabled && document.activeElement === document.body) {
      if (scroll <= 0) {
        scrollLeft();
      }
    }
  };

  const init = () => {
    navigator.mediaDevices.getUserMedia({ video: true, audio: true })
      .then((stream) => {
        setInitiated(true);
        setScrollEnabled(true);
        window.requestAnimationFrame(() => {
          scrollRight();
        });
      })
      .catch((err) => {
        console.log('fuck');
      })
  };

  useKeyPressEvent('ArrowRight', handleArrowRight, handleArrowRight);
  useKeyPressEvent('ArrowLeft', handleArrowLeft, handleArrowLeft);

  return (
    <>
      <div className="vignette" />
      <div
        className="App"
        ref={el}
        onWheel={handleWheel}
        style={{ transform: `translateX(${scroll * 100}vw)` }}
      >
        <Entry callback={init} />
        {initiated && (
          <>
            <Surveillance />
            <Marginalia />
            <Discord />
            <Grima />
          </>
        )}
      </div>
    </>
  );
}

export default App;
